<template>
  <div class="login-wrap">
    <div class="ms-login">
      <div class="ms-title">仓单业务风险管理系统</div>
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="0px"
        class="ms-content"
      >
        <el-form-item prop="username">
          <el-input
            v-model="ruleForm.username"
            placeholder="用户名"
            @change="function(modelVal){ruleForm.username=modelVal}"
          >
            <el-button
              slot="prepend"
              icon="el-icon-all-user"
            ></el-button>
          </el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            type="password"
            placeholder="密码"
            v-model="ruleForm.password"
            @keyup.enter.native="login('ruleForm')"
            @change="function(modelVal){ruleForm.password=modelVal}"
          >
            <el-button
              slot="prepend"
              icon="el-icon-all-password"
            ></el-button>
          </el-input>
        </el-form-item>
        <div class="login-select">
          <el-checkbox
            v-model="checked"
            style="color:#a0a0a0;"
          >记住帐户</el-checkbox>
          <span
            @click="findPwd"
            class="forgetPwd"
          >忘记密码</span>
        </div>
        <div class="login-btn">
          <el-button
            type="primary"
            @click="login('ruleForm')"
          >登录</el-button>
        </div>
        <p class="login-tips">Tips : 请输入用户名和密码。</p>
      </el-form>
      <el-dialog
        title="忘记密码"
        :visible.sync="forgetPwdVisible"
        width="300px"
        :append-to-body='true'
      >
        <el-form
          :model="forgetPwdData"
          ref="form"
          :rules="rules"
          label-width="80px"
        >
          <el-form-item
            label="手机号"
            prop="phone"
          >
            <el-input
              type="text"
              v-model.trim="forgetPwdData.phone"
            ></el-input>
          </el-form-item>
          <el-form-item
            label="验证码"
            prop="code"
            width="30%"
          >
            <el-input
              type="text"
              v-model.trim="forgetPwdData.code"
              style="width:80px"
            ></el-input>
            <el-button
              :disabled="disabled"
              @click="sendcode"
            >{{btntxt}}</el-button>
          </el-form-item>
          <el-form-item
            label="新密码"
            prop="newPwd"
          >
            <el-input
              type="password"
              v-model.trim="forgetPwdData.newPwd"
            ></el-input>
          </el-form-item>
          <el-form-item
            label="确认密码"
            prop="newPwdConfirm"
          >
            <el-input
              type="password"
              v-model.trim="forgetPwdData.newPwdConfirm"
              placeholder="请重复新密码"
            ></el-input>
          </el-form-item>
        </el-form>
        <div
          slot="footer"
          class="dialog-footer"
        >
          <el-button @click="forgetPwdVisible = false">取 消</el-button>
          <el-button
            type="primary"
            @click="onSubmit"
          >确 定</el-button>
        </div>
      </el-dialog>
      <el-dialog
        title="绑定手机"
        :visible.sync="bindingDialog"
        width="300px"
      >
        <el-form
          :model="bindForm"
          ref="bindForm"
          :rules="rules"
          label-width="80px"
        >
          <el-form-item
            label="手机号"
            prop="phone"
          >
            <el-input
              type="text"
              v-model.trim="bindForm.phone"
            ></el-input>
          </el-form-item>
          <el-form-item
            label="验证码"
            prop="code"
            width="30%"
          >
            <el-input
              type="text"
              v-model.trim="bindForm.code"
              style="width:80px"
            ></el-input>
            <el-button
              :disabled="bindForm.disabled"
              @click="bindsendcode"
            >{{bindForm.btntxt}}</el-button>
          </el-form-item>
        </el-form>
        <div
          slot="footer"
          class="dialog-footer"
        >
          <el-button @click="bindingDialog = false">取 消</el-button>
          <el-button
            type="primary"
            @click="bindFormSubmit"
          >确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import * as ajaxUrl from "../ajax.config.js"; // 引入接口地址

export default {
  data() {
    //修改密码二次验证
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        if (this.forgetPwdData.newPwdConfirm !== "") {
          this.$refs.form.validateField("newPwdConfirm");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请再次输入密码"));
      } else if (value !== this.forgetPwdData.newPwd) {
        callback(new Error("两次输入的密码不一致"));
      } else {
        callback();
      }
    };
    return {
      checked: false,
      ruleForm: {
        username: "",
        password: ""
      },
      forgetPwdVisible: false,
      bindingDialog: false,
      forgetPwdData: {
        phone: "",
        code: "",
        newPwd: "",
        newPwdConfirm: ""
      },
      bindForm: {
        username: "",
        phone: "",
        code: "",
        btntxt: "获取验证码",
        disabled: false,
        time: 0
      },
      btntxt: "获取验证码",
      disabled: false,
      time: 0,
      rules: {
        phone: [{ required: true, message: "请输入手机号", trigger: "blur" }],
        code: [{ required: true, message: "请输入验证码", trigger: "blur" }],
        newPwd: [{ required: true, validator: validatePass, trigger: "blur" }],
        newPwdConfirm: [
          { required: true, validator: validatePass2, trigger: "blur" }
        ]
      }
    };
  },
  mounted() {
    this.getUsername();
  },
  methods: {
    login(formName) {
      if (this.checked) {
        localStorage.setItem("username", this.ruleForm.username);
      }
      this.$refs[formName].validate(valid => {
        if (valid) {
          let postData = {
            username: this.ruleForm.username,
            password: this.ruleForm.password
          };
          // localStorage.setItem("aide_username", "res.data.resData.name");
          // this.$router.push("/");
          this.axios
            .post(ajaxUrl.LOGIN, this.$qs.stringify(postData),{
              headers: {
                "Content-Type": "application/x-www-form-urlencoded"
              }
            })
            .then(res => {
              if (res.data.resCode == 200) {
                localStorage.setItem(
                  "aide_username",
                  res.data.resData.userName
                );
                sessionStorage.setItem("userId", res.data.resData.id);
                this.bindForm.userId = res.data.resData.id;
                if (res.data.resData.mobile) {
                  this.$router.push("/");
                } else {
                  this.bindingDialog = true;
                  this.bindForm.username = localStorage.getItem(
                    "aide_username"
                  );
                }
              } else {
                this.$message.error(res.data.resMsg);
              }
            })
            .catch(err => {
              console.log(err);
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    sendcode() {
      let params = {
        phone: this.forgetPwdData.phone,
        smsType: "forget"
      };
      this.time = 60;
      this.disabled = true;
      this.timer();
      this.axios
        .post(ajaxUrl.SENDMESSAGE, params)
        .then(res => {
          if (res.data.resCode == 200) {
            this.$message({
              message: res.data.resMsg,
              type: "success"
            });
          } else {
            this.$message.error(res.data.resMsg);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },

    timer() {
      if (this.time > 0) {
        this.time--;
        this.btntxt = this.time + "s";
        setTimeout(this.timer, 1000);
      } else {
        this.time = 0;
        this.btntxt = "获取验证码";
        this.disabled = false;
      }
    },

    bindsendcode() {
      let params = {
        phone: this.bindForm.phone,
        smsType: "register"
      };
      this.bindForm.time = 60;
      this.bindForm.disabled = true;
      this.bindtimer();

      this.axios
        .post(ajaxUrl.SENDMESSAGE, params)
        .then(res => {
          if (res.data.resCode == 200) {
            this.$message({
              message: res.data.resMsg,
              type: "success"
            });
          } else {
            this.$message.error(res.data.resMsg);
          }
        })
        .catch(err => {
          console.log(err);
        });
      // if (this.bindForm.phone == "") {
      //   this.$message({
      //     message: "请输入绑定手机号",
      //     type: "warning"
      //   });
      //   return;
      // }
    },

    bindtimer() {
      if (this.bindForm.time > 0) {
        this.bindForm.time--;
        this.bindForm.btntxt = this.bindForm.time + "s";
        setTimeout(this.bindtimer, 1000);
      } else {
        this.bindForm.time = 0;
        this.bindForm.btntxt = "获取验证码";
        this.bindForm.disabled = false;
      }
    },

    onSubmit() {
      this.$refs.form.validate(valid => {
        if (valid) {
          let params = {
            phone: this.forgetPwdData.phone,
            smsType: "forget",
            code: this.forgetPwdData.code,
            newPassword: this.forgetPwdData.newPwd
          };
          this.axios.post(ajaxUrl.FORGETMYPASSWORD, params)
          .then(res => {
            if (res.data.resCode == 200) {
              this.$router.push("/login");
              this.forgetPwdVisible = false;
              this.$message({
                message: res.data.resMsg,
                type:"success"
              })
            } else {
              this.$message.error(res.data.resMsg);
            }
          })
          .catch(err => {
            console.log(err);
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    //忘记密码
    findPwd() {
      this.forgetPwdVisible = true;
    },

    //取得localStorrage记住的用户名
    getUsername() {
      this.ruleForm.username = localStorage.getItem("aide_username");
    },

    bindFormSubmit() {
      this.$refs.bindForm.validate(valid => {
        if (valid) {
          let params = {
            phone: this.bindForm.phone,
            smsType: "register",
            code: this.bindForm.code,
            userId: this.bindForm.userId
          };
          this.axios.post(ajaxUrl.VALIDCODE, this.$qs.stringify(params), {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded"
            }
          })
          .then(res => {
            if (res.data.resCode == 200) {
              this.$router.push("/");
            } else {
              this.$message.error(res.data.resMsg);
            }
          })
          .catch(err => {
            console.log(err);
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>

<style scoped>
.login-wrap {
  position: relative;
  width: 100%;
  height: 100%;
  background-image: url(../assets/images/login-bg.jpg);
  background-size: 100%;
}
.ms-title {
  width: 100%;
  line-height: 50px;
  text-align: center;
  font-size: 20px;
  color: #fff;
  border-bottom: 1px solid #ddd;
}
.ms-login {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 350px;
  margin: -190px 0 0 -175px;
  border-radius: 5px;
  background: rgba(255, 255, 255, 0.3);
  overflow: hidden;
}
.ms-content {
  padding: 30px 30px;
}
.login-btn {
  text-align: center;
  margin-top: 10px;
}
.login-btn button {
  width: 100%;
  height: 36px;
  margin-bottom: 10px;
}
.login-tips {
  font-size: 12px;
  line-height: 30px;
  color: #fff;
}
.forgetPwd {
  cursor: pointer;
  color: #f19149;
  font-size: 0.9rem;
  margin-left: 145px;
}
</style>


